

.fa-sort-alpha-down {
    color: rgb(153, 160, 153);
    font-size: 20px;
    &:hover {
      color: rgb(175, 248, 157);
    }
  }
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: rgb(181, 211, 173);
  }
  .header__title {
    font-size: 60px;
    font-family: "Harry Potter", sans-serif;
    margin-bottom: 0px;
    -webkit-text-stroke: 1.5px rgb(0, 0, 0);
    background: linear-gradient(rgb(203, 229, 192), rgb(15, 15, 15));
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .header__subtitle {
    margin-top: 0px;
    font-family: "IM Fell English SC", serif;
    font-size: 25px;
  }
  
  .main {
    &__container {
      display: flex;
      justify-content: center;
    }
    &__section {
      &--container {
        display: flex;
        height: 500px;
        justify-content: center;
        width: 100%;
        flex-wrap: wrap;
        &--element {
          width: 300px;
          height: 300px;
        }
      }
      &--element {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      &--name {
        color: rgb(210, 233, 210);
        font-size: 20px;
        padding: 5px;
        font-family: "IM Fell English SC", serif;
      }
      &--image {
        height: 200px;
        width: 150px;
        border-radius: 8px;
        border: 2px solid rgb(95, 94, 94);
      }
      &--list {
        padding-top: 5px;
        padding-bottom: 5px;
      }
    }
  }
  .section {
    background: linear-gradient(
      rgba(108, 110, 105, 0.795),
      rgba(15, 15, 15, 0.808)
    );
    border-radius: 12px;
    padding: 8px;
    margin-left: 12px;
    height: 380px;
    color: rgb(198, 207, 202);
    &__title {
      padding: 8px;
      margin: 0px;
      font-family: "IM Fell English SC", serif;
    }
    &__filter {
      display: flex;
      flex-direction: column;
      width: 160px;
      justify-content: center;
      &--button {
        margin: auto;
        padding-top: 8px;
        padding-bottom: 22px;
        width: 30px;
        background-color: transparent;
        border: transparent;
        cursor: pointer;
        cursor: hand;
      }
    }
  }
  .filter {
    
    &__label {
      font-size: 20px;
      padding: 8px;
    }
    &__input {
      font-size: 18px;
      background: darkgrey;
      border-radius: 6px;
      border: 1px solid darkgrey;
      width: 140px;
      font-family: 'Markazi Text', serif;
      padding-left: 8px;
    }
    &__select {
      font-family: 'Markazi Text', serif;
      font-size: 18px;    
      height: 28px;
      width: 140px;
      background: darkgrey;
      border-radius: 6px;
      border: 1px solid darkgrey;
      margin-bottom: 12px;
      
    }
  }
  
  .details {
    display: flex;
    background: linear-gradient(rgba(108, 110, 105, 0.76), rgba(0, 0, 0, 0.534));
    border-radius: 12px;
    width: 400px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include tablet {
      width: 70%;
      display: flex;
      flex-direction: row;
    }
    @include desktop {
      width: 80%;
      display: flex;
      flex-direction: row;
       
    }
    &__img {
      margin: 30px;
      width: 200px;
      height: 280px;
      border-radius: 12px;
      border: 3px solid rgb(27, 27, 27);
    }
    &__list {
      font-size: 20px;
      color: rgb(253, 255, 253);
      font-family: "IM Fell English SC", serif;
      &--name {
        font-size: 35px;
        margin-top: 20px;
        margin-bottom: 50px;
        text-align: center;
        color: rgb(24, 23, 23);
      }
      &--div {
        display: flex;
        justify-content: flex-start;
      }
      &--emblem {
        margin-top: 8px;
        width: 130px;
        height: 170px;
        margin-bottom: 8px;
        
       
      }
    }
  }
  .fa-heartbeat {
    color: rgb(235, 43, 52);
  }
  
  .backLink {
    color: rgb(38, 61, 38);
    &:hover {
      color: rgb(175, 248, 157);
    }
  }
  
  .rest__button {
    background-color: rgb(84, 87, 84);
    color: rgb(201, 201, 201);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3px;
    width: 140px;
    height: 30px;
    border-radius: 8px;
    border: transparent;
    cursor: pointer;
    cursor: hand;
    font-family: "Markazi Text", serif;
    font-size: 16px;
    &:hover {
      background-color: rgb(201, 201, 201);
      color: rgb(84, 87, 84);
    }
  }
  